import React, { Component } from 'react';
import { NavLink } from 'react-router-dom';

import NavItem from '../../component/NavItem/NavItem';
import classes from './NavBar.module.css';

class Navbar extends Component {

    render() {
        return (
            <nav className={classes.Navbar}>
                <ul>
                    <li key='Portfolio'>
                        <NavLink to='/myportfolio' style={{textDecoration: 'none'}}><NavItem>Portfolio</NavItem></NavLink>
                    </li>
                    <li key='Resume'>
                        <NavLink to='/resume' style={{textDecoration: 'none'}}><NavItem>Resume</NavItem></NavLink>
                    </li>
                </ul>
            </nav>
        )
    }
}

export default Navbar;